.register {
    display: flex;
    align-items: center;
    justify-content: center;
}

.register__container {
    width:45vw;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 10px;
}

.register__textBox {
    padding: 10px;
    font-size: 18px;
    margin-bottom: 10px;
    border-radius: 10px;
    color: black;
    background-color: white;
}

.loginLink{
    cursor: pointer;
    text-underline-position: below;
}
.register__btn{
    background-color: black;
    color: white;
    padding: 15px;
    border-radius: 15px;
}
.seperator{
    width: 100%; 
    text-align: center; 
    border-bottom: 1px solid #000; 
    line-height: 0.1em;
    margin: 10px 0 20px; 
    padding: 10px;
 } 
 
 .seperator span {  
     padding:0 10px; 
 }

.register__google{
    display: inline;
    margin: 10px;
    background-color: white;
    color:black;
    border-radius: 14px;
    align-items: center;
    justify-content: center;
}

.header h2 {
    font-family: stag;
    font-size: 32px;
    padding-bottom: 5vh;
    color: white;
  }

.register div{
    margin-top: 7px;
}

.loginRegisterName{
    text-align: left;
}

.loginRegisterEmail{
    text-align: left;
}
.loginRegisterPassword{
    text-align: left;
}